.movie-card {
  
  &__link {
    color: inherit;
    font-weight: 500;
font-size: 12px;
line-height: 1.33;

@media screen and (min-width: 1280px) {
    font-size: 20px;
    line-height: 1.2;
    &:hover .movie-card__holder,
    &:focus .movie-card__holder
    {
       scale: 1.03;
    }
  }
  }

  &__holder {
    width: 100%;
    height: 402px;
    border-radius: 5px;
    overflow: hidden;
    margin-bottom: 10px;
    transition: scale 250ms cubic-bezier(0.4, 0, 0.2, 1);
   
    

    @media screen and (min-width: 768px) {
      height: 455px;
      margin-bottom: 8px;
    }

    @media screen and (min-width: 1280px) {
      height: 574px;
      margin-bottom: 12px;
      
    }
  }

  &__img {
    object-fit: fill;
    height: 100%;
  }

  &__title{
text-transform: uppercase;
}

  &__genre {   
    color: var(--secondary-text-color);    
  }

//   &__date {
    
